/* general */
*, body{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: Arial, Helvetica, sans-serif;
}
body{
    background-color: rgb(248, 248, 248);
}
.container{
    width: 80%;
    margin: 0 auto;
}

/* navbar */
.navbar{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    padding: 25px 0;
    z-index: 999;
}
.navbar .logo{
    display: inline-block;
    font-size: 25px;
    text-transform: capitalize;
    color: #fff;
}
.navbar .menu-container{
    float: right;
    position: relative;
}
.navbar .menu-container .menu-switch{
    display: block;
    width: 35px;
    height: 19px;
    position: relative;
    cursor: pointer;
}
.navbar .menu-container .menu-switch .overlay{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
}
.navbar .menu-container .menu-switch .bar{
    display: block;
    width: 100%;
    height: 3px;
    background-color: #D5C455;
    position: absolute;
    transition: all .5s ease-in-out;
}
.navbar .menu-container .menu-switch .bar:first-child{
    top: 0;
    left: 0;
}
.navbar .menu-container .menu-switch .bar:nth-child(2){
    top: 8px;
    left: 0;
}
.navbar .menu-container .menu-switch .bar:nth-child(3){
    bottom: 0;
    right: 0;
}

.navbar .menu-container .menu-switch.open .bar{
    background-color: rgb(255, 47, 47);
}

.navbar .menu-container .menu-switch.open .bar:first-child{
    transform: rotate(405deg);
    top: 8px;
    left: 0;
}
.navbar .menu-container .menu-switch.open .bar:nth-child(2){
    display: none;
}
.navbar .menu-container .menu-switch.open .bar:nth-child(3){
    transform: rotate(315deg);
    top: 8px;
    left: 0;
}
.navbar .menu-container .menu{
    position: absolute;
    top: 40px;
    right: 0;
    width: 200px;
    text-align: center;
    background-color: #fff;
    overflow: hidden;
    max-height: 0;
    transition: ease-in-out .25s all;
    padding: 0;
    text-transform: capitalize;
}
.navbar .menu-container .menu.open{
    max-height: 100vh;
}

.navbar .menu-container .menu ul{
    list-style: none;
    padding: 20px 0;
}
.navbar .menu-container .menu ul li{
    width: 80%;
    margin: 0 auto 5px;
    cursor: pointer;
    padding-bottom: 5px;
    position: relative;
}
.navbar .menu-container .menu ul li a{
    text-decoration: none;
    color: inherit;
}
.navbar .menu-container .menu ul li::after{
    display: block;
    position: absolute;
    content: "";
    bottom: -5px;
    left: 0;
    width: 0;
    height: 2px;
    background-color: #D5C455;
    transition: all .5s ease-in-out;
}
.navbar .menu-container .menu ul li:hover::after{
    width: 100%;
}
.navbar .menu-container .menu ul li:hover a{
    color: #D5C455;
}
.navbar .menu-container .menu ul li:last-of-type{
    margin: 0 auto;
}
.navbar .menu-container .menu ul li.active{
    color: #D5C455;
    border-bottom: 2px solid #D5C455;
}
.navbar .menu-container .menu ul li.active::after{
    display: none;
}
/* home view */
.home{
    height: 100vh;
    width: 100vw;
    padding: 0;
    margin: 0;
    display: flex;
    color: #fff;
}
.home .column{
    width: 50%;
    height: 100%;
    padding: 0;
    margin: 0;
}
.home .c-1{
    background-color: #D5C455;
    display: flex;
    flex-direction: column;
    justify-content: center;
    box-shadow: 5px 0px 42px 0px rgba(0,0,0,0.8);
    -webkit-box-shadow: 5px 0px 42px 0px rgba(0,0,0,0.8);
    -moz-box-shadow: 5px 0px 42px 0px rgba(0,0,0,0.8);
    z-index: 10;
}
.home .c-1 span,
.home .c-1 h2{
    padding-left: 25%;
}
.home .c-1 span{
    text-transform: uppercase;
    font-size: 20px;
    display: block;
}
.home .c-1 h2{
    text-transform: capitalize;
    font-size: 50px;
}
.home .c-2{
    background-image: url(./images/pexels-pixabay-270404.jpg);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    position: relative;
}
.home .c-2 .overlay{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .3);
}

/* about view */

/* about me section */
.about h2.heading{
    padding: 250px 0 150px;
    text-align: center;
    text-transform: capitalize;
    font-size: 50px;
    background-image: url('./images/about-banner.jpg');
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    position: relative;
    color: #fff;
}
.about h2.heading span{
    position: relative;
    z-index: 5;
}
.about h2.heading::after{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 1;
}
.about .about-me .container{
    display: flex;
    padding: 120px 0;
}
.about .about-me .container .column-without-space{
    width: 50%;
}
.about .about-me .container .column-without-space.col-1{
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}
.about .about-me .container .column-without-space.col-1 .statment-box{
    width: 40%;
    padding: 60px 0;
    background-color: #fff;
    margin: 20px 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.about .about-me .container .column-without-space.col-1 .statment-box h3.heading{
    font-size: 45px;
    color: #D5C455;
}
.about .about-me .container .column-without-space.col-1 .statment-box .title{
    font-size: 17px;
    font-weight: 100;
    color: #999;
    text-align: center;
    text-transform: capitalize;
}
.about .about-me .container .column-without-space.col-1 .statment-box .title a{
    text-decoration: none;
}
.about .about-me .container .column-without-space.col-2{
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.about .about-me .container .column-without-space.col-2 .sub-heading{
    color: #D5C455;
    font-size: 17.5px;
    margin-bottom: 0;
    text-transform: uppercase;
}
.about .about-me .container .column-without-space.col-2 h3.heading{
    font-size: 45px;
    text-transform: uppercase;
    margin-bottom: 20px;
}
.about .about-me .container .column-without-space.col-2 .introduction{
    font-size: 18.5px;
    color: #999;
    margin-bottom: 20px;
    text-transform: capitalize;
}
.about .about-me .container .column-without-space.col-2 .status{
    font-size: 18.5px;
    color: #999;
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-wrap: wrap;
}
.about .about-me .container .column-without-space.col-2 .status .state{
    text-transform: capitalize;
    width: 40%;
}
.about .about-me .container .column-without-space.col-2 .status .state span{
    color: #D5C455;
    font-weight: bold;
    display: flex;
}
.about .about-me .container .column-without-space.col-2 .status .state.email span{
    text-transform: lowercase;
}

/* my skills section */
.skills .title{
    font-size: 50px;
    text-align: center;
    text-transform: uppercase;
}
.skills .container{
    display: flex;
    justify-content: space-between;
    padding: 0 0 120px 0;
    flex-wrap: wrap;
    width: 50%;
}
.skills .container .skill{
    width: 30%;
    margin: 25px 0;
}
.skills .container .skill .skill-name{
    margin-bottom: 10px;
    font-size: 17px;
    text-transform: uppercase;
    color: #666;
}
.skills .container .skill .bar{
    width: 100%;
    border-radius: 5px;
    position: relative;
    height: 5px;
    background-color: #aaa;
}
.skills .container .skill .bar .percentage{
    position: absolute;
    right: 0;
    top: -25px;
    font-size: 17px;
    color: #D5C455;
}
.skills .container .skill .bar .expertise-bar{
    position: absolute;
    left: 0;
    top: 0;
    height: 5px;
    border-radius: 5px;
    background-color: #D5C455;
}

/* services */
.services h2.heading{
    padding: 250px 0 150px;
    text-align: center;
    text-transform: capitalize;
    font-size: 50px;
    background-image: url('./images/service.jpg');
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    position: relative;
    color: #fff;
}
.services h2.heading span{
    position: relative;
    z-index: 5;
}
.services h2.heading::after{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 1;
}
.services .container{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.services .container .service{
    width: 25%;
    padding: 10px 10px;
}
.services .container .service .ser-container{
    margin-bottom: 20px;
    background-color: #fff;
    padding: 50px 20px;
    border-radius: 10px;
    box-shadow: 0px 5px 25px -2px rgb(0 0 0 / 6%);
    transition: all .5s ease-in-out;
    text-transform: capitalize;
}
.services .container .service .ser-container:hover{
    background-color: #D5C455;
    color: #fff;
}
.services .container .service .ser-container .icon{
    text-align: center;
    font-size: 50px;
    color: #D5C455;
    margin-bottom: 20px;
}
.services .container .service .ser-container .title{
    text-align: center;
    font-size: 25px;
    font-weight: 300;
    margin-bottom: 10px;
    
}
.services .container .service .ser-container p{
    font-weight: 100;
    font-size: 17px;
    color: #999;
}
.services .container .service .ser-container:hover .title,
.services .container .service .ser-container:hover p,
.services .container .service .ser-container:hover .icon{
    color: #fff;
}

/* contact me */
.contact h2.heading{
    padding: 250px 0 150px;
    text-align: center;
    text-transform: capitalize;
    font-size: 50px;
    background-image: url('./images/contact.jpg');
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    position: relative;
    color: #fff;
}
.contact h2.heading span{
    position: relative;
    z-index: 5;
}
.contact h2.heading::after{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 1;
}
.contact .container{
    padding: 120px 0;
    text-transform: capitalize;
    font-size: 35px;
}
.contact .container h3{
    margin: 0 0 20px;
}
.contact .container h3 span{
    text-transform: lowercase;
}


/* responsive */
@media (max-width: 1024px) {
    .home{
        flex-direction: column;
    }
    .home .column{
        width: 100%;
    }
    .home .c-1{
        order: 2;
    }
    .home .c-2{
        order: 1;
    }
    .services .container .service{
        width: 100%;
        text-align: center;
    }
}
@media (max-width: 768px) {
    .home .c-1 span{
        font-size: 19px;
        padding: 0;
        text-align: center;
    }
    .home .c-1 h2{
        font-size: 30px;
        padding: 0;
        text-align: center;
    }
    .home .c-2{
        order: 1;
    }
    .about .about-me .container{
        flex-direction: column;
    }
    .about .about-me .container .column-without-space{
        width: 100%;
    }
    .about .about-me .container .column-without-space.col-2 h3.heading{
        font-size: 30px;
    }
    .about .about-me .container .column-without-space.col-1{
        order: 2;
    }
    .about .about-me .container .column-without-space.col-2{
        order: 1;
    }
    .about .about-me .container .column-without-space.col-2 .status .state{
        width: 90%;
    }
    .about .about-me .container .column-without-space.col-1 .statment-box{
        width: 90%;
        padding: 10px 0;
    }
    .skills .title{
        font-size: 30px;
    }
    .skills .container{
        width: 100%;
        justify-content: space-around;
    }
    .skills .container .skill{
        width: 45%;
    }
    .contact .container{
        font-size: 25px;
    }
}

@media (max-width: 425px){
    .skills .container .skill{
        width: 90%;
    }
    .contact .container{
        font-size: 18px;
    }
}